					<el-input v-model="ruleForm.operation" placeholder="用户操作" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="请求方法" prop="method">
