				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="请求方法" prop="method">
					<el-input v-model="ruleForm.method" placeholder="请求方法" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="请求时长(毫秒)" prop="time">
					<el-input v-model="ruleForm.time" placeholder="请求时长(毫秒)" clearable  :readonly="ro.time"></el-input>
