				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="设备编号" prop="shebeibianhao">
					<el-input v-model="ruleForm.shebeibianhao" placeholder="设备编号" clearable  :readonly="ro.shebeibianhao"></el-input>
