				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="设备名称" prop="shebeimingcheng">
					<el-input v-model="ruleForm.shebeimingcheng" placeholder="设备名称" clearable  :readonly="ro.shebeimingcheng"></el-input>
