<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				biaoti : false,
				fengmiantupian : false,
				jishuyuanzhanghao : false,
				jishuyuan : false,
				jishuyuanshouji : false,
				pHzhi : false,
				zhidi : false,
				yangfenhanliang : false,
				shuifenzhuangkuang : false,
				wendu : false,
				shidu : false,
				rizhaoshishu : false,
				jiangshuiliang : false,
				zhongzhijihua : false,
				fabushijian : false,
				clicktime : false,
				clicknum : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				biaoti: '',
				fengmiantupian: '',
				jishuyuanzhanghao: '',
				jishuyuan: '',
				jishuyuanshouji: '',
				pHzhi: '',
				zhidi: '',
				yangfenhanliang: '',
				shuifenzhuangkuang: '',
				wendu: '',
				shidu: '',
				rizhaoshishu: '',
				jiangshuiliang: '',
				zhongzhijihua: '',
				fabushijian: '',
				clicktime: '',
			},
		

			
			rules: {
				biaoti: [
					{ required: true, message: '标题不能为空', trigger: 'blur' },
				],
				fengmiantupian: [
				],
				jishuyuanzhanghao: [
				],
				jishuyuan: [
				],
				jishuyuanshouji: [
				],
				pHzhi: [
				],
				zhidi: [
				],
				yangfenhanliang: [
				],
				shuifenzhuangkuang: [
				],
				wendu: [
				],
				shidu: [
				],
				rizhaoshishu: [
				],
				jiangshuiliang: [
				],
				zhongzhijihua: [
				],
				fabushijian: [
				],
				clicktime: [
				],
				clicknum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
		this.ruleForm.fabushijian = this.getCurDate()
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='biaoti'){
							this.ruleForm.biaoti = obj[o];
							this.ro.biaoti = true;
							continue;
						}
						if(o=='fengmiantupian'){
							this.ruleForm.fengmiantupian = obj[o];
							this.ro.fengmiantupian = true;
							continue;
						}
						if(o=='jishuyuanzhanghao'){
							this.ruleForm.jishuyuanzhanghao = obj[o];
							this.ro.jishuyuanzhanghao = true;
							continue;
						}
						if(o=='jishuyuan'){
							this.ruleForm.jishuyuan = obj[o];
							this.ro.jishuyuan = true;
							continue;
						}
						if(o=='jishuyuanshouji'){
							this.ruleForm.jishuyuanshouji = obj[o];
							this.ro.jishuyuanshouji = true;
							continue;
						}
						if(o=='pHzhi'){
							this.ruleForm.pHzhi = obj[o];
							this.ro.pHzhi = true;
							continue;
						}
						if(o=='zhidi'){
							this.ruleForm.zhidi = obj[o];
							this.ro.zhidi = true;
							continue;
						}
						if(o=='yangfenhanliang'){
							this.ruleForm.yangfenhanliang = obj[o];
							this.ro.yangfenhanliang = true;
							continue;
						}
						if(o=='shuifenzhuangkuang'){
							this.ruleForm.shuifenzhuangkuang = obj[o];
							this.ro.shuifenzhuangkuang = true;
							continue;
						}
						if(o=='wendu'){
							this.ruleForm.wendu = obj[o];
							this.ro.wendu = true;
							continue;
						}
						if(o=='shidu'){
							this.ruleForm.shidu = obj[o];
							this.ro.shidu = true;
							continue;
						}
						if(o=='rizhaoshishu'){
							this.ruleForm.rizhaoshishu = obj[o];
							this.ro.rizhaoshishu = true;
							continue;
						}
						if(o=='jiangshuiliang'){
							this.ruleForm.jiangshuiliang = obj[o];
							this.ro.jiangshuiliang = true;
							continue;
						}
						if(o=='zhongzhijihua'){
							this.ruleForm.zhongzhijihua = obj[o];
							this.ro.zhongzhijihua = true;
							continue;
						}
						if(o=='fabushijian'){
							this.ruleForm.fabushijian = obj[o];
							this.ro.fabushijian = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
				


















			}
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
					if(((json.jishuyuanzhanghao!=''&&json.jishuyuanzhanghao) || json.jishuyuanzhanghao==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.jishuyuanzhanghao = json.jishuyuanzhanghao
						this.ro.jishuyuanzhanghao = true;
					}
					if(((json.jishuyuan!=''&&json.jishuyuan) || json.jishuyuan==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.jishuyuan = json.jishuyuan
						this.ro.jishuyuan = true;
					}
					if(((json.jishuyuanshouji!=''&&json.jishuyuanshouji) || json.jishuyuanshouji==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.jishuyuanshouji = json.jishuyuanshouji
						this.ro.jishuyuanshouji = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `zhongzhijihua/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.zhongzhijihua = this.ruleForm.zhongzhijihua.replace(reg,'../../../springboot110s8rtj/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {


	if(this.ruleForm.fengmiantupian!=null) {
		this.ruleForm.fengmiantupian = this.ruleForm.fengmiantupian.replace(new RegExp(this.$base.url,"g"),"");
	}

















var objcross = this.$storage.getObj('crossObj');

      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
       this.$refs["ruleForm"].validate(valid => {
         if (valid) {
		 if(crossrefid && crossuserid) {
			 this.ruleForm.crossuserid = crossuserid;
			 this.ruleForm.crossrefid = crossrefid;
			let params = { 
				page: 1, 
				limit: 10, 
				crossuserid:this.ruleForm.crossuserid,
				crossrefid:this.ruleForm.crossrefid,
			} 
			this.$http({ 
				url: "zhongzhijihua/page", 
				method: "get", 
				params: params 
			}).then(({ 
				data 
			}) => { 
				if (data && data.code === 0) { 
				       if(data.data.total>=crossoptnum) {
					     this.$message.error(this.$storage.get('tips'));
					       return false;
				       } else {
					 this.$http({
					   url: `zhongzhijihua/${!this.ruleForm.id ? "save" : "update"}`,
					   method: "post",
					   data: this.ruleForm
					 }).then(({ data }) => {
					   if (data && data.code === 0) {
					     this.$message({
					       message: "操作成功",
					       type: "success",
					       duration: 1500,
					       onClose: () => {
						 this.parent.showFlag = true;
						 this.parent.addOrUpdateFlag = false;
						 this.parent.zhongzhijihuaCrossAddOrUpdateFlag = false;
						 this.parent.search();
						 this.parent.contentStyleChange();
					       }
					     });
					   } else {
					     this.$message.error(data.msg);
					   }
					 });

				       }
				} else { 
				} 
			});
		 } else {
			 this.$http({
			   url: `zhongzhijihua/${!this.ruleForm.id ? "save" : "update"}`,
			   method: "post",
			   data: this.ruleForm
			 }).then(({ data }) => {
			   if (data && data.code === 0) {
			     this.$message({
			       message: "操作成功",
			       type: "success",
			       duration: 1500,
			       onClose: () => {
				 this.parent.showFlag = true;
				 this.parent.addOrUpdateFlag = false;
				 this.parent.zhongzhijihuaCrossAddOrUpdateFlag = false;
				 this.parent.search();
				 this.parent.contentStyleChange();
			       }
			     });
			   } else {
			     this.$message.error(data.msg);
			   }
			 });
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.zhongzhijihuaCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    fengmiantupianUploadChange(fileUrls) {
	    this.ruleForm.fengmiantupian = fileUrls;
    },
  }
};
